<template>
  <div id="toolBar" @mousewheel="scroll">
    <img src="../../icons/svg/save.svg" @click="newButton" />
    <img src="../../icons/svg/edit.svg" @click="editButton" :class="(selectedEditing)?'selecetedImg':null" />
    <img src="../../icons/svg/new.svg" @click="saveButton" :class="(selectState==='addText')?'selecetedImg':null"/>
    <img src="../../icons/svg/preview.svg" @click="previewButton" :class="(selectedPacking)?'selecetedImg':null"/>
    <el-dropdown>
    <span class="el-dropdown-link">
      Dropdown List
      <el-icon class="el-icon--right">
        <arrow-down />
      </el-icon>
    </span>
    <template #dropdown>
      <el-dropdown-menu>
        <el-dropdown-item><button @click="saveRoll">save as roll</button></el-dropdown-item>
        <el-dropdown-item><button @click="saveTrigger">save as trigger</button></el-dropdown-item>
        <el-dropdown-item><button @click="bigger">bigger</button></el-dropdown-item>
        <el-dropdown-item><button @click="smaller">smaller</button></el-dropdown-item>
        <el-dropdown-item divided><button @click="saveFile">save </button></el-dropdown-item>
        <el-dropdown-item><button @click="loadFile">load </button></el-dropdown-item>
        <el-dropdown-item><button @click="circlePacking">circlePacking </button></el-dropdown-item>
        <el-dropdown-item><button @click="scrolly">scrolly!</button></el-dropdown-item>
        <el-dropdown-item><button @click="preview">preview</button></el-dropdown-item>
        
      </el-dropdown-menu>
    </template>
  </el-dropdown>   
    <button @click="unbind">unbind</button>
    <button @click="addLine">line</button>
    <button @click="addingRect">rect</button>
    <button @click="border">border</button>
    <el-color-picker v-model="fill" :predefine="predefineColors" show-alpha/>
    <el-input-number v-model="opacity" :min="0" :max="1" :step="0.1"/>
    <el-input v-model="uploadJson" style="position:absolute;width:100px"/>
  </div>
  <div id="textEditorToolBar"/>
  <div id='charticulatorToolBar'/>
</template>

<script>
import * as d3 from 'd3'
import {dfsCircles,bundleDoms,polyPacking,circlePacking} from '../../util/util'
import '@wangeditor/editor/dist/css/style.css'
import {ref} from 'vue'

export default {
  name:'toolBar',
  data(){
    return{
      opacity:1,
      uploadJson:'{}',
      fill:ref('rgba(0,0,0,0.5)'),
      predefineColors : ref([
        '#ff4500',
        '#ff8c00',
        '#ffd700',
        '#90ee90',
        '#00ced1',
        '#1e90ff',
        '#c71585',
        'rgba(255, 69, 0, 0.68)',
        'rgb(255, 120, 0)',
        'hsv(51, 100, 98)',
        'hsva(120, 40, 94, 0.5)',
        'hsl(181, 100%, 37%)',
        'hsla(209, 100%, 56%, 0.73)',
        '#c7158577',
        'rgba(22, 141, 217, 1)',
        'rgb(189, 189, 189)'
      ])
    }
  },
  computed:{
    selectedEditing(){
      return this.$store.state.editing;
    },
    selectState(){
      return this.$store.state.selectState;
    },
    initCharticulator(){
      return this.$store.state.initCharticulator;
    },
    selectedPacking(){
      return this.$store.state.packing;
    }
  },
  mounted(){
    // const editorToolbar=createToolbar({
    //   editor:,
    //   selector:'#textEditorToolBar',
    //   mode:'default',
    // })
    // new E('#textEditorToolBar');
    // this.$store.commit('createEditor',(editor));
    // editor.config.onchange=(newTextHTML)=>{
    //   this.$store.commit('newTextHTML',{newTextHTML});
    //   this.$store.commit('updateKeyFrameEdited');
    // }
    // editor.config.uploadImgShowBase64 = true;
    // editor.create();
  },
  watch:{
    fill(val){
      console.log(val)
      this.$store.commit('updataFill',{fill:val});
    },
    selectedEditing(val){
      if (!val){
        const circles=dfsCircles(document.querySelector('#mainCanvas'));
        const doms=circles;
        this.$store.commit('updateDoms',{doms})
        const that=this
        circles.forEach((circle,id) => {
          d3.select(circle)
          .on('click',null)
          .on('mouseover',()=>that.$store.commit('selectCircle',{id}));
        });
      }
    },
    initCharticulator(init){
      if (init){
      const charticulatorToolBar=document.querySelector('.charticulator__toolbar-horizontal');
      document.querySelector('#charticulatorToolBar').appendChild(charticulatorToolBar);
      document.querySelector('#mainCanvas').children[0].setAttribute("transform","translate(280,180) scale(0.6)");
      }
    },
    opacity(val){
      this.$store.commit('changeOpacity',{'opacity':val});
    }
  },
  methods:{
    addingRect(){
      this.$store.commit('addingRect');
    },
    border(){
      this.$store.commit('addBorder');
    },
    addLine(){
      
      this.$store.commit('addingLine');
    },
    preview(){
      
    },
    unbind(){
      this.$store.commit('unbind');
    },
    scroll(e){
      //focusTime存的是真实距离
      //focus传递的需要是svg上的距离
      const focusTime=Math.min(Math.max(1,this.$store.state.focusTime+(e.deltaY/4)),this.$store.state.svgHeightMax)
      console.log(this.$store.state.focusTime,focusTime,e.deltaY);
      this.$store.commit('focus',{
        oldTime:this.$store.state.focusTime,
        focusTime,
        type:'scrolly'
      });
    },
    scrolly(){
      this.$store.commit('beginScrolly');
    },
    circlePacking(){
      circlePacking(null,this.$store.state.selectedDoms)
    },
    saveFile(){
      this.$store.commit('saveFile');
    },
    loadFile(){
      this.$store.commit('loadFile',{'uploadJson':this.uploadJson})
    },
    saveRoll(){
      this.$store.commit('saveAsRoll');
    },
    saveTrigger(){
      this.$store.commit('saveAsTrigger');
    },
    clickCircle(id){
      console.log(id)
    },
    newButton(){
      console.log(this.$store.state);
    },
    editButton(){
      this.$store.commit('clickEditing');
    },
    saveButton(){
      console.log(this.$store.state);
      if (this.selectState!=='addText')
        this.$store.commit('updateSelectState',{selectState:'addText'})
      else
        this.$store.commit('updateSelectState',{selectState:'normal'})
    },
    previewButton(){
      this.$store.commit('clickPacking');
      // this.$store.commit('updateDrawPackingState');
      // polyPacking([],this.$store.state.selectedDoms);
    }
  }
}
</script>

<style lang="sass">
#toolBar
  z-index: 99
  position: absolute
  width: 1512px
  height: 67px
  left: 0px
  top: 0px
  background: #000000
  border: 1px solid #000000
  box-sizing: border-box



img
  margin: 5px 50px
  cursor: pointer

.selecetedImg
  border: 1px solid #FFF
  border-radius: 10px

#textEditorToolBar
  position: absolute
  width: 1536px
  height: 150px
  left: 0px
  bottom: 0px

#charticulatorToolBar
  position: absolute
  width: 1512px
  height: 20px
  left: 0px
  top: 67px
  z-index: 99


</style>

