<template>
  <div id="datasetView"/>
</template>

<script>
import { doc } from 'prettier'
export default {
  name:'dataSet',
  data(){
    return {
      
    }
  },
  computed:{
    initCharticulator(){
      return this.$store.state.initCharticulator;
    }
  },
  watch:{
    initCharticulator(init){
      if (init){
      const charticulatorDataSet=document.querySelectorAll('.minimizable-panel-view')[0];
      document.querySelector('#datasetView').appendChild(charticulatorDataSet);
      }
    }
  },
  mounted(){
    }
}
</script>

